import React from 'react'
import './skills.css'
import SkillCard from '../SkillCard/SkillCard'

function Skills({skillsData}) {
    return (
        <div className="skills-container">
            <h2>Industrial Knowledge</h2>
            <div className="group-skills-container">
                {
                    skillsData.industryKnowledge.map((skill, index) => {
                        return(
                            <SkillCard key={index} skill={skill} />
                        )
                    })
                }
            </div>
            <h2>Tech Skills</h2>
            <div className="group-skills-container">
                {
                    skillsData.techSkills.map((skill, index) => <SkillCard key={index} skill={skill} />)
                }
            </div>
            <h2>Interpersonal Skills</h2>
            <div className="group-skills-container">
                {
                    skillsData.interpersonalSkills.map((skill, index) => <SkillCard key={index} skill={skill} />)
                }
            </div>
        </div>
    )
}

export default Skills
