import React from 'react'
import './sidebar.css'

function Sidebar({handleSectionClick}) {
    return (
        <div className="sidenav">
            <img
                src={require('../../assets/img/uStoreCEO.png')}
                className="avatar"
                alt="avatar"
            />
            <h2 className="main-name">Shubham Kumar Singh</h2>
            <p className="subtitle">A Mechanical Engineer Who Codes!</p>
            <div className="sections-list">
                <p className="section-list-element" onClick={() => handleSectionClick("About Me")}>About Me</p>                
                <p className="section-list-element" onClick={() => handleSectionClick("Skills")}>Skills</p>                
                <p className="section-list-element" onClick={() => handleSectionClick("Projects")}>Projects</p>                
                <p className="section-list-element" onClick={() => handleSectionClick("Experiences")}>Experiences</p>                
            </div>
        </div>
    )
}

export default Sidebar
