import React, {useState, useEffect} from 'react'
import './portfolio.css'
import Sidebar from '../Sidebar/Sidebar'
import Section from '../Section/Section'
import {about} from '../../constants/About'
import {skills} from '../../constants/Skills'
import {projects} from '../../constants/Projects'
import {experiences} from '../../constants/Experiences'

function Portfolio() {
    const [heading, setHeading] = useState("About Me")
    const [sectionData, setSectionData] = useState()


    useEffect(() => {
        if(heading === "About Me"){
            setSectionData(about)
        }else if(heading === "Skills"){
            setSectionData(skills)
        }else if(heading === "Projects"){
            setSectionData(projects)
        }else if(heading === "Experiences"){
            setSectionData(experiences)
        }
    }, [heading])
    

    const handleSectionClick = (sectionHeading) => {
        setHeading(sectionHeading)
    }

    return (
        <div className="portfolio-container">
           <div className="sidenav-container">
               <Sidebar handleSectionClick = {handleSectionClick}/>
           </div>
           <div className="main-section-container">
               {
                   sectionData !== undefined ? <Section sectionData={sectionData} /> : null
               }
           </div>
        </div>
    )
}

export default Portfolio
