import React from 'react'
import './section.css'
import About from '../About/About'
import Skills from '../Skills/Skills'
import {about} from '../../constants/About'
import {skills} from '../../constants/Skills'
import {projects} from '../../constants/Projects'
import Projects from '../Projects/Projects'
import Experiences from '../Experiences/Experiences'
import { experiences } from '../../constants/Experiences'

function Section({sectionData}) {
    return (
        <div className="section-container">
            <div className="section-heading-container">
                <h1 className="main-heading">{sectionData.heading}</h1>
                <p className="main-quote">{sectionData.quote}</p>
            </div>
            <div className="section-component">
                {
                    {
                        "About Me" : <About aboutData={about} />,
                        "Skills" : <Skills skillsData={skills} />,
                        "Projects" : <Projects projectsData={projects} />,
                        "Experiences" : <Experiences experiencesData={experiences} />
                    }[sectionData.heading]
                }
            </div>
        </div>
    )
}

export default Section
